
body {
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

/* desktop */

.m1 {
    display: flex;
    flex-direction: column;
    align-items: center;
}

/* header */
.h1 {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    height: 62px;
}
.h11 {
    width: auto;
    display: flex;
    margin-left: 25px;
}

.h11 div {
    width: 24px;
    cursor: pointer;
}
.h11 p {
    margin-left: 15px;
    margin-top: -1px;
    font-size: 16px;
}
.h12 {
    width: 100px;
    position: absolute;
    margin-left: 50%;
    transform: translateX(-50%);
}
.h13 {
    width: 30px;
    margin-right: 20px;
}
.progressBar {
    width: 100%;
    background-color: rgb(248, 248, 248);
}
.progressBar div {
    width: 10%;
    height: 3px;
    background-color: #FFE147;
}

/* body */
.b1 {
    width: 100%;
    max-width: 1200px;
    float: left;
}

.b11 {
    float: left;
    width: 100%;
}

.b11_d {
    float: left;
    width: calc(65% - 40px);
    padding: 0 20px;
}
.b12_d {
    float: left;
    width: 35%;
}

.b11_d50 {
    float: left;
    width: calc(50%);
    padding: 0 20px;
    background-color: #FAFAFA;
}
.b12_d50 {
    float: left;
    width: 50%;
}

.b11_d50 img {
    float: left;
    margin-top: 60px;
    margin-left: 50%;
    transform: translateX(-50%);
    background-color: white;
}
.b111 {
    float: left;
    width: 100%;
    text-align: center;
    margin-top: 20px;
    font-size: 24px;
    font-weight: bold;
    margin-top: 60px;
}
.b1_button_section {
    margin-top: 50px;
    width: 110px;
    float: right;
    margin-bottom: 50px;
}
.contButton {
    display: flex;
    padding: 5px 5px 5px 7px;
    width: 110px;
    flex-direction: row-reverse;
    background-color: #FFE147;
    border-radius: 50px;
    cursor: pointer;
}
.contButton img {
    width: 16px;
    rotate: 180deg;
    margin: 5px;
}
.contButton span {
    margin-top: 1px;
    margin-right: 5px;
    font-size: 14px;
    font-weight: 600;
}

.s1_cont {
    float: left;
    width: 100%;
    max-width: 900px;
    width: 90%;
    margin-left: 50%;
    transform: translateX(-50%);
    margin-top: 70px;
}

.s1_t1 {
    float: left;
    width: calc(50% - 90px);
}
.s1_t1 div {
    float: left;
    width: 100%;
    font-weight: 500;
    font-size: 16px;
}
.s1_t1 input {
    background-color: #F7F7F7;
    border-radius: 10px;
    font-size: 14px;
    float: left;
    width: 100%;
    padding: 9px 10px;
    color: black;
    outline: none;
}
.s1_t1 button {
    float: left;
    width: auto;
    background-color: #FFE147;
    padding: 7px 12px;
    border-radius: 7px;
    font-size: 11px;
    font-weight: 500;
    margin-top: 7px;
    cursor: pointer;
}

.s1_t2 {
    float: left;
    width: 27px;
    height: 27px;
