    margin-top: 24px;
}
.s1_pu_mcont {
    position: fixed;
    width: 100%;
    height: 100%;
    background-color: #d9d9d93c;
    backdrop-filter: blur(2px);
    display: flex;
    justify-content: center;
    align-items: center;
}
.s1_pu_cont {
    width: 414px;
    height: 81px;
    background-color: #FFFBE5;
