/* custom input */
input[type="checkbox"] {
    display: none;
}
input[type="checkbox"] + .s2_sel_item {
    background-color: #E8E8E8;
    border: 1px solid #D2D2D2;
}

input[type="checkbox"]:checked + .s2_sel_item {
    background-color: #ffe0473a;
    border: 1px solid #FFE147;
}


